* {
	box-sizing: border-box;
}

html,
body,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
img,
i,
ul,
li,
fieldset,
form,
label,
footer,
header,
main {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font-family: inherit;
	vertical-align: baseline;
	user-select: none;
}
/* HTML5 display-role reset for older browsers */
footer,
header,
main {
	display: block;
}

html {
	min-width: 320px;
	font-family: "Montserrat", sans-serif;
}

body {
	line-height: 1;
}

h1 {
	font-family: "Comfortaa", cursive;
}

ul {
	list-style: none;
}

button {
	background-color: transparent;
	border: none;
	padding: 0;
	margin: 0;
	cursor: pointer;
	outline: none;
	font-family: inherit;
	vertical-align: center;
}
